            <th>产品编码</th>
            <th>品名</th>
            <th>类别</th>
            <th>状态</th>
            <th>产品尺寸(cm)</th>
            <th>包装尺寸(cm)</th>
            <th>重量(kg)</th>
            <th>体积</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tableList" :key="index">
            <td>
              <div>
                <span></span>
                <input
                  @change="changeSelect"
