                  :value="item.id"
                  class="allcheck"
                />
              </div>
            </td>
            <!-- @click="goProductInformationDetails(item.product_code)" -->
            <td>
              {{ index + 1 + (page - 1) * 50 }}
            </td>
            <td>
              {{ item.product_code }}
            </td>
            <td
              title="点击品名进入详情"
              @click="goProductInformationDetails($event, item.product_code)"
            >
              {{ item.product_name }}
            </td>
            <td>
              {{ item.product_type }}
            </td>
            <td>
              {{ item.product_state }}
            </td>
            <td>
              {{ item.product_size }}
            </td>
            <td>
              {{ item.product_package_size }}
            </td>
            <td>
              {{ item.product_weight }}
            </td>
            <td>
              {{ item.volume }}
            </td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        v-if="pageShow"
        background
        @current-change="handleCurrentChange"
        :current-page.sync="page"
        layout="total,prev, pager, next, jumper"
        :total="total"
        :page-size="50"
        class="pagetotle"
        style="
          margin: 0 auto;
          width: 600px;
          text-align: center;
          background: #fff;
        "
      >
      </el-pagination>
    </footer>
    <el-dialog
      title="编辑产品"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="false"
      width="400px"
      center
    >
      <!-- <span class="dialogSpan">
        <div class="dialogSpanLeft">
          <div style="margin-top: 0.5vh">产品编码</div>
          <div style="margin-top: 2vh">品名</div>
          <div style="margin-top: 4.5vh">类别</div>
          <div style="margin-top: 2vh">产品尺寸</div>
          <div style="margin-top: 2vh">包装尺寸</div>
          <div style="margin-top: 1.8vh">重量(kg)</div>
          <div style="margin-top: 1.8vh">状态</div>
        </div>
        <div class="dialogSpanRight">
          <div>
            <input type="text" v-model="editProductId" readonly />
          </div>
