              rows="10"
              v-model="editProductName"
              readonly
            ></textarea>
          </div>
          <div>
            <input type="text" v-model="editProductType" readonly />
          </div>
          <div>
            <input
              type="text"
              style="width: 2vw"
              v-model="editProductSize1"
              @keyup.enter="editSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="editProductSize2"
              @keyup.enter="editSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="editProductSize3"
              @keyup.enter="editSubmit"
            />
          </div>
          <div>
            <input
              type="text"
              style="width: 2vw"
              v-model="editProductPackSize1"
              @keyup.enter="editSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="editProductPackSize2"
              @keyup.enter="editSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="editProductPackSize3"
              @keyup.enter="editSubmit"
            />
          </div>
          <div>
            <input
              type="text"
              v-model="editproductWeight"
              @keyup.enter="editSubmit"
            />
          </div>
          <div>
            <select name="" id="" v-model="editState" @keyup.enter="editSubmit">
              <option
                v-for="(item, index) in productStateList"
                :key="index"
                :value="item.value"
              >
                {{ item.value }}
              </option>
            </select>
          </div>
        </div>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editSubmit()">保 存</el-button>
        <el-button type="warning" @click="editReset()">重 置</el-button>
      </span> -->
      <el-form :inline="true">
        <el-form-item label="产品编码">
          <el-input readonly v-model="editProductId"></el-input>
        </el-form-item>
        <el-form-item label="品名">
          <el-input
            type="textarea"
            :rows="3"
            readonly
            v-model="editProductName"
          ></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <el-input readonly v-model="editProductType"></el-input>
        </el-form-item>
        <el-form-item label="产品尺寸" class="sizeList">
          <!-- <el-input
            @keydown.native="InputNum"
            class="no-number"
            type="number"
            v-model="addProductSize1"
          ></el-input> -->
          <el-input v-model="editProductSize1"></el-input>
          <span>*</span>
          <el-input v-model="editProductSize2"></el-input>
          <span>*</span>
          <el-input v-model="editProductSize3"></el-input>
        </el-form-item>
        <el-form-item label="包装尺寸" class="sizeList">
          <el-input v-model="editProductPackSize1"></el-input>
          <span>*</span>
          <el-input v-model="editProductPackSize2"></el-input>
          <span>*</span>
          <el-input v-model="editProductPackSize3"></el-input>
        </el-form-item>
        <el-form-item label="重量(kg)">
          <el-input v-model="editproductWeight"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="editState">
            <el-option
              v-for="(item, index) in productStateList"
              :key="index"
              :value="item.value"
              :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="lastBtnGroup">
          <el-button type="primary" @click="editSubmit()">提 交</el-button>
          <el-button type="warning" @click="editReset()">重 置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="新增产品"
      :visible.sync="centerDialogVisible2"
      width="400px"
      :close-on-click-modal="false"
      center
    >
      <!-- <span class="dialogSpan">
        <div class="dialogSpanLeft">
          <div style="margin-top: 0.5vh" class="addID">产品编码</div>
          <div style="margin-top: 2vh">品名</div>
          <div style="margin-top: 4.5vh">类别</div>
          <div style="margin-top: 2vh">产品尺寸</div>
          <div style="margin-top: 2vh">包装尺寸</div>
          <div style="margin-top: 1.8vh">重量(kg)</div>
          <div style="margin-top: 1.8vh">状态</div>
        </div>
        <div class="dialogSpanRight">
          <div>
            <input
              type="text"
              v-model="addProductId"
              @keyup.enter="addSubmit"
              @focus="returnBackground()"
            />
          </div>
          <div>
            <textarea
              name=""
              id=""
              cols="30"
              rows="10"
              v-model="addProductName"
            ></textarea>
          </div>
          <div>
            <select
              name=""
              id=""
              v-model="addProductType"
              @keyup.enter="addSubmit"
            >
              <option
                v-for="(item, index) in productTypeList"
                :value="item.value"
                :key="index"
              >
                {{ item.value }}
              </option>
            </select>
          </div>
          <div>
            <input
              type="text"
              style="width: 2vw"
              v-model="addProductSize1"
              @keyup.enter="addSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="addProductSize2"
              @keyup.enter="addSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="addProductSize3"
              @keyup.enter="addSubmit"
            />
          </div>
          <div>
            <input
              type="text"
              style="width: 2vw"
              v-model="addProductPackSize1"
              @keyup.enter="addSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="addProductPackSize2"
              @keyup.enter="addSubmit"
            />*<input
              type="text"
              style="width: 2vw"
              v-model="addProductPackSize3"
              @keyup.enter="addSubmit"
            />
          </div>
          <div>
            <input
              type="text"
              v-model="productWeight"
              @keyup.enter="addSubmit"
            />
          </div>
          <div>
            <select name="" id="" v-model="addState" @keyup.enter="addSubmit">
              <option
                v-for="(item, index) in productStateList"
                :key="index"
                :value="item.value"
              >
                {{ item.value }}
              </option>
            </select>
          </div>
        </div>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addSubmit()">提 交</el-button>
        <el-button type="warning" @click="addReset()">重 置</el-button>
      </span> -->
      <el-form :inline="true">
        <el-form-item label="产品编码">
          <el-input v-model="addProductId"></el-input>
        </el-form-item>
        <el-form-item label="品名">
          <el-input
            :rows="3"
            type="textarea"
            v-model="addProductName"
          ></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <!-- <el-input v-model="addProductType"></el-input> -->
          <el-select v-model="addProductType">
            <el-option
              v-for="(item, index) in productTypeList"
              :key="index"
              :value="item.value"
              :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="产品尺寸" class="sizeList">
          <!-- <el-input
            @keydown.native="InputNum"
            class="no-number"
            type="number"
            v-model="addProductSize1"
          ></el-input> -->
          <el-input v-model="addProductSize1"></el-input>
          <span>*</span>
          <el-input v-model="addProductSize2"></el-input>
          <span>*</span>
          <el-input v-model="addProductSize3"></el-input>
        </el-form-item>
        <el-form-item label="包装尺寸" class="sizeList">
          <el-input v-model="addProductPackSize1"></el-input>
          <span>*</span>
          <el-input v-model="addProductPackSize2"></el-input>
          <span>*</span>
          <el-input v-model="addProductPackSize3"></el-input>
        </el-form-item>
        <el-form-item label="重量(kg)">
          <el-input v-model="productWeight"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="addState">
            <el-option
              v-for="(item, index) in productStateList"
              :key="index"
              :value="item.value"
              :label="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="lastBtnGroup">
          <el-button type="primary" @click="addSubmit()">提 交</el-button>
          <el-button type="warning" @click="addReset()">重 置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// import { scrollTopTable } from "@/utils/utils.js";
import axios from "axios";
export default {
  data() {
    return {
      pageShow: true,
      pageFlag: false,
      centerDialogVisible: false,
      centerDialogVisible2: false,
      tableList: [], //表格数据
      channelList: [], //平台下拉框
      productTypeList: [], //类别下拉框
      productStateList: [], //平台下拉框
      addProductId: "", //新增产品编码
      addProductName: "", //新增产品品名
      addProductType: "", //新增产品类别
      addProductPackSize1: "",
      addProductPackSize2: "",
      addProductPackSize3: "", //包装尺寸
      addProductSize1: "",
      addProductSize2: "",
      addProductSize3: "", //产品尺寸
      productWeight: "", //产品质量
      addState: "", //新增状态
      editProductId: "", //编辑界面产品编码
      editProductName: "", //编辑界面产品品名
      editProductType: "", //编辑界面产品类别
      editProductPackSize1: "",
      editProductPackSize2: "",
      editProductPackSize3: "", //编辑界面包装尺寸
      editProductSize1: "",
      editProductSize2: "",
      editProductSize3: "", //编辑界面产品尺寸
      editproductWeight: "", //编辑界面产品质量
      editState: "", //编辑界面状态
      // searchState: "", //下拉框状态
      // searchType: "", //下拉框类别

      total: 0, //总页数
      page: 1, //页码当前页

      searchData: "", //搜索,
      currentSelect: ""
    };
  },
  methods: {
    // 切换选中的input
    changeSelect(e) {
      console.log("切换input");

      let allInput = document.querySelectorAll(".allcheck");
      if (this.currentSelect) {
        // for (let i = 0; i < allInput.length; i++) {
        //   allInput[i].checked = false;
        // }
        this.currentSelect.checked = false;
      }
      if (this.currentSelect !== e.target) {
        e.target.checked = true;
        this.currentSelect = e.target;
      } else {
        this.currentSelect = "";
      }
    },
    getTableList(val) {
      // 修改
      if (val && val === "新增") {
        let lastPage = Math.ceil((this.total + 1) / 50);
        console.log(
          "是不是同一页",
          Math.ceil(this.total / 50),
          lastPage,
          Math.ceil(this.total / 50) !== lastPage
        );
        if (Math.ceil(this.total / 50) !== lastPage) {
          // 两个不相同
          this.pageShow = false;
          this.pageFlag = true;
        }
        this.page = lastPage;
      }
      if (val && val === "删除") {
        // 删除
        let lastTotal = this.total - 1;
        console.log("删除后条数", lastTotal);
        console.log("总条数", this.total);
        if (lastTotal < 50 * this.page) {
          // 删除后条数 小于当前页数
          this.page = Math.ceil(lastTotal / 50);
        }
      }
      //获取表格和下拉框数据
      axios({
        url: this._url2 + "server/databases/product_information",
        methods: "get",
        params: {
          page: this.page
        }
      }).then(res => {
        this.tableList = res.data.data;
        console.log(res, 55);
        this.channelList = res.data.platform_data;
        this.productTypeList = res.data.product_type_data;
        this.productStateList = res.data.product_state_data;
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.$nextTick(() => {
          if (this.pageFlag) {
            this.pageShow = true;
          }
        });
      });
    },
    openAdd() {
      //打开增加弹框
      this.centerDialogVisible2 = true;
      this.addProductId = "";
      this.addProductName = "";
      this.addProductType = "";
      this.addProductSize1 = "";
      this.addProductSize2 = "";
      this.addProductSize3 = "";
      this.addProductPackSize1 = "";
      this.addProductPackSize2 = "";
      this.addProductPackSize3 = "";
      this.addState = "";
      this.productWeight = "";
    },
    addReset() {
      //新增弹框重置
      this.addProductId = "";
      this.addProductName = "";
      this.addProductType = "";
      this.addProductSize1 = "";
      this.addProductSize2 = "";
      this.addProductSize3 = "";
      this.addProductPackSize1 = "";
      this.addProductPackSize2 = "";
      this.addProductPackSize3 = "";
      this.addState = "";
      this.productWeight = "";
    },
    isNum(num) {
      return /^(\d+|\d+\.\d+)$/g.test(num);
    },
    addSubmit() {
      for (let i = 1; i <= 3; i++) {
        let key = "addProductSize" + i;
        let key1 = "addProductPackSize" + i;
        console.log("产品尺寸", this.isNum(this[key]));
        console.log("包装尺寸", this.isNum(this[key1]));
        if (
          (this[key] && !this.isNum(this[key])) ||
          (this[key1] && !this.isNum(this[key1])) ||
          (this.productWeight && !this.isNum(this.productWeight))
        ) {
          alert("产品尺寸/包装尺寸/重量请输入数字！");
          return;
        }
      }

      //增加
      if (
        this.addProductId == "" ||
        this.addProductName == "" ||
        this.addProductType == "" ||
        this.addState == ""
      ) {
        alert("请填写产品编码，品名，类别，状态");
      } else {
        let prosize =
          this.addProductSize1 +
          "*" +
          this.addProductSize2 +
          "*" +
          this.addProductSize3;
        let propacksize =
          this.addProductPackSize1 +
          "*" +
          this.addProductPackSize2 +
          "*" +
          this.addProductPackSize3;
        let formData = new FormData();
        console.log(prosize);
        console.log(propacksize);
        formData.append("product_code", this.addProductId);
        formData.append("product_name", this.addProductName);
        formData.append("product_type", this.addProductType);
        formData.append("platform", this.addChannel);
        formData.append("product_size", prosize);
        formData.append("product_package_size", propacksize);
        formData.append("product_weight", this.productWeight);
        formData.append("product_state", this.addState);
        formData.append("platform", "");
        this.$axios
          .post(this._url2 + "server/databases/product_information", formData)
          .then(res => {
            if (res.data.code == 200) {
              this.centerDialogVisible2 = false;
              alert("新增成功");
              this.getTableList("新增");
            } else if (res.data.code == 500) {
              alert(res.data.msg);
              // document.querySelector(".addID").style.background = "red";
            }
          });
      }
    },
    deletProduct() {
      //删除函数
      let allcheckli = document.querySelectorAll(".allcheck");
      let demo = [];
      for (let i = 0; i < allcheckli.length; i++) {
        if (allcheckli[i].checked == true) {
          demo.push(allcheckli[i].value);
        }
      }
      if (demo.length < 1) {
        alert("请选择您想要的数据删除");
      } else {
        this.$confirm("此操作将永久删除选中商品, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$axios
              .delete(this._url2 + "server/databases/product_information", {
                params: {
                  id: demo
                }
              })
              .then(res => {
                if (res.data.code == 200) {
                  this.$message({
                    type: "success",
                    message: "删除成功!"
                  });
                  this.getTableList("删除");
                }
              });
          })
          .catch(() => {});
      }
    },
    openEdit() {
      //获取选择到的产品编码和品名
      let flag = 0;
      let allcheckli = document.querySelectorAll(".allcheck");
      for (let i = 0; i < allcheckli.length; i++) {
        if (allcheckli[i].checked == true) {
          flag++;
        }
      }
      if (flag == 1) {
        this.centerDialogVisible = true;
        console.log(this.tableList);
        for (let i = 0; i < allcheckli.length; i++) {
          if (allcheckli[i].checked == true) {
            for (let j = 0; j < this.tableList.length; j++) {
              if (allcheckli[i].value == this.tableList[j].id) {
                this.editProductName = this.tableList[j].product_name;
                this.editProductId = this.tableList[j].product_code;
                this.editproductWeight = this.tableList[j].product_weight;
                if (this.tableList[j].product_size) {
                  let _str = this.tableList[j].product_size;
                  let strs = _str.split("*");
                  this.editProductSize1 = strs[0];
                  this.editProductSize2 = strs[1];
                  this.editProductSize3 = strs[2];
                }
                if (this.tableList[j].product_package_size) {
                  let _str2 = this.tableList[j].product_package_size;
                  let strs2 = _str2.split("*");
                  this.editProductPackSize1 = strs2[0];
                  this.editProductPackSize2 = strs2[1];
                  this.editProductPackSize3 = strs2[2];
                }
                this.editProductType = this.tableList[j].product_type;
                this.editState = this.tableList[j].product_state;
              }
            }
          }
        }
      } else {
        alert("请只选择一条数据编辑");
      }
    },
    editReset() {
      //编辑框重置
      this.editProductSize1 = "";
      this.editProductSize2 = "";
      this.editProductSize3 = "";
      this.editProductPackSize1 = "";
      this.editProductPackSize2 = "";
      this.editProductPackSize3 = "";
      this.editState = "";
      this.editproductWeight = "";
    },
    editSubmit() {
      //修改值
      if (this.editState == "") {
        alert("请填写状态");
      } else {
        let allcheckli = document.querySelectorAll(".allcheck");
        let id = "";
        for (let i = 0; i < allcheckli.length; i++) {
          if (allcheckli[i].checked == true) {
            id = allcheckli[i].value;
          }
        }
        let formData = new FormData();
        let prosize1 =
          this.editProductSize1 +
          "*" +
          this.editProductSize2 +
          "*" +
          this.editProductSize3;
        let propacksize1 =
          this.editProductPackSize1 +
          "*" +
          this.editProductPackSize2 +
          "*" +
          this.editProductPackSize3;
        formData.append("id", id);
        console.log(id);
        formData.append("product_code", this.editProductId);
        formData.append("product_name", this.editProductName);
        formData.append("product_type", this.editProductType);
        formData.append("platform", this.editChannel);
        formData.append("product_size", prosize1);
        formData.append("product_package_size", propacksize1);
        formData.append("product_weight", this.editproductWeight);
        formData.append("product_state", this.editState);
        this.$axios
          .put(this._url2 + "server/databases/product_information", formData)
          .then(res => {
            if (res.data.code == 200) {
              alert("修改成功");
              this.getTableList();
            }
          });
        this.centerDialogVisible = false;
      }
    },
    search() {
      //搜索按钮
      let params = {
        condition: this.searchData,
        page: 1
      };
      axios({
        url: this._url2 + "server/databases/product_information",
        methods: "get",
        params: params
      }).then(res => {
        console.log(res);
        this.tableList = res.data.data;
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.page = 1;
        let allcheckli = document.querySelectorAll(".allcheck");
        for (let i = 0; i < allcheckli.length; i++) {
          allcheckli[i].checked = false;
        }
      });
    },
    goProductInformationDetails(e, product_code) {
      let target = e.target;
      if (target.constructor !== HTMLTableCellElement) return;
      this.$store.commit("getIpQuery", {
        condition: this.searchData,
        page: this.page
      });
      this.$router.push({
        path: `productInformationDetails`,
        query: {
          id: product_code
          // queryObj: {
          //   page: this.page,
          //   condition: this.searchData
          // }
        }
      });
      // if(target.textContent === '')
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      let params = {
        condition: this.searchData,
        page: this.page
      };
      axios({
        url: this._url2 + "server/databases/product_information",
        methods: "get",
        params: params
      }).then(res => {
        console.log("切换页数", res);
        console.log(this.productStateList);
        this.tableList = res.data.data;
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.productStateList = res.data.product_state_data || [];
        this.productTypeList = res.data.product_type_data || [];
        // 翻页滚动到表头
        this.$nextTick(() => {
          $(".PI_table >tbody").scrollTop(0);
        });
      });
      let allcheckli = document.querySelectorAll(".allcheck");
      for (let i = 0; i < allcheckli.length; i++) {
        allcheckli[i].checked = false;
      }
    }
    // returnBackground() {
    //   // document.querySelector(".addID").style.background = "";
    // }
    // 要求输入数字
    // InputNum(e) {
    //   if (e.key === "e" || e.key === "-") {
    //     e.returnValue = false;
    //   }
    //   return true;
    // }
  },

  created() {
    console.log(this.$store.state);

    let query_obj = this.$store.state.PI_Query;
    console.log(query_obj);
    if (query_obj && query_obj.page !== "") {
      let item = query_obj;
      // this.page = query_obj.page;
      this.searchData = item.condition;
      this.handleCurrentChange(item.page);
    } else {
      this.getTableList();
    }
  }
};
</script>

<style scoped>
table {
  /*width: 84vw;*/
  margin: 10px auto;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 65vh;
  width: fit-content;
  overflow-y: auto;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 8vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

.title {
  border-top: #e5e5e5 1px solid;
  border-left: #e5e5e5 1px solid;
  border-right: #e5e5e5 1px solid;
  height: 40px;
  line-height: 40px;
  text-align: center;
}

header {
  display: flex;
  width: 60vw;
  height: 10vh;
  margin: 1vh auto;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.9);
}

header div {
  flex: 1;
  text-align: center;
  line-height: 10vh;
}

header div select {
  width: 8vw;
  height: 3vh;
}
input {
  outline: none;
}
table tr th:nth-child(1),
table tr td:nth-child(1),
table tr th:nth-child(2),
table tr td:nth-child(2) {
  width: 4vw;
}

table tr td:nth-child(2),
table tr td:nth-child(3),
table tr td:nth-child(4),
table tr td:nth-child(5),
table tr td:nth-child(6),
table tr td:nth-child(7),
table tr td:nth-child(8),
table tr td:nth-child(9) {
  cursor: pointer;
}

table tr th:nth-child(3),
table tr td:nth-child(3) {
  width: 12vw;
}

table tr th:nth-child(4),
table tr td:nth-child(4) {
  width: 16vw;
}

table tr th:nth-child(5),
table tr td:nth-child(5),
table tr th:nth-child(6),
table tr td:nth-child(6) {
  width: 7vw;
}

table tr th:nth-child(7),
table tr td:nth-child(7),
table tr th:nth-child(8),
table tr td:nth-child(8),
table tr th:nth-child(9),
table tr td:nth-child(9) {
  width: 10vw;
}

.dialogSpan {
  display: flex;
}

.dialogSpanLeft {
  flex: 3;
}

.dialogSpanLeft div {
  text-align: right;
  font-size: 0.8rem;
}

.dialogSpanRight {
  flex: 7;
}

textarea {
  height: 5vh;
  width: 9vw;
  margin: 5px;
}

.dialogSpan input,
.dialogSpan select {
  margin: 5px;
  width: 9vw;
  height: 3vh;
}

.Btn {
  float: left;
  width: 140px;
  padding: 8px;
  margin: 5px 10px;
}
</style>
<style>
#productInformation .el-form {
  text-align: right;
  margin-right: 13px;
}
#productInformation .el-form-item.lastBtnGroup {
  text-align: center;
  width: 100%;
  margin-right: 0;
}
#productInformation .el-form .el-select,
#productInformation .el-form textarea {
  width: 202px;
  resize: none;
}
#productInformation .el-form-item.sizeList {
  display: flex;
  justify-content: flex-end;
}
#productInformation .el-form-item.sizeList input {
  width: 56px;
}
#productInformation .sizeList .el-form-item__content {
  display: flex;
}
#productInformation .sizeList span {
  margin: 0 5px;
}
</style>



